* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
  }
  
  body {
    font-family: "Segoe UI", Arial, sans-serif;
    line-height: 1.4;
    color: #444;
    height: 100vh;
  }

  .container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    /* border: 2px solid red; */
  }

  .inner {
    width: 100%;
    padding: 8rem 10px 4rem;
    /* border: 2px solid black; */
  }

  .form-container {
    width: 100%;
    margin-bottom: 20px;
    display: flex;
    border-radius: calc(0.5 * 100px);
    box-shadow: 0 1px 6px 0 rgba(0, 0, 0, 0.38);
    justify-content: space-evenly;
  }

  .input-text {
    font-size: 1rem;
    font-weight: 400;
    width: 85%;
    padding-right: 5px;
    padding-left: 10px;
    border-radius: calc(0.5 * 100px);
  }

  .input-text::placeholder {
    color: rgba(148, 34, 110, 0.699);
  }

  .input-submit {
    background: transparent;
    color: #5b5b5b;
    text-transform: capitalize;
    cursor: pointer;
    font-weight: 600;
    margin-right: 10px;
  }

  .input-text,
.input-submit {
  height: 45px;
  outline: none;
  border: none;
}

.item {
    font-size: 1.2rem;
    list-style-type: none;
    padding: 3% 0px;
    border-bottom: 1px solid #eaeaea;
  }

  .checkbox {
    margin-right: 15px;
  }
  
  .item button {
    font-size: 13px;
    background: #f1f3f4;
    border: none;
    cursor: pointer;
    float: right;
    outline: none;
    border-radius: 100px;
    height: 50px;
    width: 50px;
    margin : -10px 0 0 10px;
  }
  
  .textInput {
    width: 100%;
    padding: 10px;
    border: 1px solid #dfdfdf;
  }